body {
    font-family: "Segoe UI";
    max-width: 38rem;
    margin: 0;
}
.main{
    display: flex;
    flex-direction: row;
    width: 100vw;
    height: 100vh;
}
.sideBar {
    background: #0487d0;
    height: 100vh;
    width: 200px;
}
.auth{
    /*background-color: #7289DA;*/
    min-width: 200px;
    background-image: url('https://i.ytimg.com/vi/5ld6inyqz1M/maxresdefault.jpg');
    background-size: cover;
    background-repeat: no-repeat;
}
.sideBar ul {
    text-align: center;
    padding: 0;
    margin: 0;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
    list-style: none
}
.sideBar li {
    padding: 20px;
}
.sideBar li:hover{
    background: #025987;
}
.sideBar a{
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: #ffffff
}
.content{
    /*background: repeating-radial-gradient(circle, #f8e8e8, #fde0cf 10px, #f3b2a1 10px, #faa681 20px);*/
    width: calc(100% - 200px);
    padding: 14px;
    overflow: auto;
}

button{
    color: white;
    border-radius: 5px;
    background-color: #0487d0;
    border: none;
    margin: 8px;
    height: 24px;
    font-size: 16px;
}
button:hover{
    background-color: #025987;
}

